import React from 'react';
import {Helmet} from 'react-helmet';
import {makeStyles} from '@material-ui/core/styles';
import Ripples from './ripples.svg';
import {Animated} from 'react-animated-css';
import { Typography } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import {ReactComponent as LogoSvgMobile} from './hundredsolidmobile.svg';
import {ReactComponent as LogoSvgDesktop} from './hundredsoliddesktop.svg'
import Graffiti from './graffiti-wall.jpg'
import './App.css';
import { useMediaQuery } from 'react-responsive';
 

const useStyles = makeStyles(theme => ({
    home: {
        backgroundImage: `linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(${Graffiti})`,
        height: '100vh',
        backgroundSize: 'cover',

    },
    herosvg: {
    
    },
    herodiv: {
        margin: 'auto',
        textAlign: 'center',
        paddingTop: '7%',

    }
}))

export default function Home() {
   const classes = useStyles()
   const isDesktopOrLaptop = useMediaQuery({ query: '(min-device-width: 1000px)'})
    return (
        <div className={classes.home}>
            <Helmet>
                <title>Keep it 100</title>
            </Helmet>
            <div className={classes.herodiv}>
                {isDesktopOrLaptop ? (
                    <LogoSvgDesktop/>
                )  : (
                    <LogoSvgMobile/>
                )}
            </div>
        </div>
    )
}